
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#customnum {
  margin-top: 20px;
}
#customnum .num {
  background: #fff;
}
#customnum .num input {
  width: 100%;
  border: none;
  padding: 10px;
}
#customnum .num input:focus {
  outline: none;
}
#customnum .smit {
  margin-top: 20px;
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  background: #04b101;
  color: #fff;
  border-radius: 10px;
  text-align: center;
  padding: 10px 0;
}
#customnum .clear {
  width: 35px;
  margin: 20px auto auto auto;
}



